.aboutme {
    width: 60%;
  text-align: center;
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
  
  align-content: center;

  margin-top: 5%;
}

.aboutme p{
    font-size: 25px;
}
.aboutme h1{
   
    font-size: 35px;
    text-decoration: underline;
    color:rgb(225, 29, 72);
    
    
}


@media screen  and (max-width:820px) {

    .aboutme {
        width: 100%;
        padding:20px;
     
      
     
    }

    .aboutme p{
       
        font-size:25px;
        margin-bottom: 30px;
       
        
    }
    
    
}
@media screen  and (max-width:420px) {

    .aboutme {
        width: 100%;
        padding:20px;
     
      
     
    }

    .aboutme p{
       
        font-size:25px;
        margin-bottom: 30px;
        text-align: center;
       
        
    }
    
    
}