.skillcss{
    border: 1px solid red;
   
   max-width: fit-content;
   text-align: center;
}

.skillcss:hover{
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    background-color:yellow;
   
}

.skillstyle{
    width: 55%;
    margin: auto;
    display:flex;
  
    gap: 40px;
    flex-wrap: wrap;
    margin-top: 60px;
  
    
}

.skillheading>h1{
    width: auto;
    text-align: center;
    font-size: 35px;
   
    color:rgb(225, 29, 72);
    margin-top: 50px;
    text-decoration: underline;

}

@media screen and (max-width:820px) {
    .skillheading{
         width: 90%;
         margin: auto;
         padding-bottom: 20px;
    }

    .skillheading>h1{
       
       margin-right: 45px;
    

    
       
        font-size: 38px;
       
        color:rgb(225, 29, 72);
        margin-top: 50px;
        text-decoration: underline;
    
    }

    .skillstyle{
        width: 80%;
        
        margin: auto;
        display:flex;
      
        gap: 50px;
        flex-wrap: wrap;
        margin-top: 60px;
        
      
        
    }

    .skillstyle img{
        width: 150px;
       
    }
    .skillcss{
        border: 1px solid red;
   
        max-width: fit-content;
         margin-left: 40px;
    }

    
}
